import $ from 'jquery';
import App from 'tamm/app';

$(() => {
    // Defaults
    window.Z_INDEX_NAV_TOGGLE = 699;
    window.Z_INDEX_SECTION = 599;
    window.Z_INDEX_TRANSITION = 499;

    new App().initialize();

    // Add class for script support
    $('html').removeClass('no-js')
             .addClass('js');
});



